import React from 'react';
import { slideshowData } from '../slideshow-R';
import "bootstrap/dist/css/bootstrap.min.css";
import "../styles.css";



var currSlide = 0; 
var isToggle = false; 


function loop(n) { 
    console.log("In loop: " + n);
    for (var i in slideshowData) {
        console.log(slideshowData[i].num + ", props: " + n);
        if(slideshowData[i].num == n) {
            console.log(slideshowData[i].title);
            var retArray = [i, slideshowData[i].title, slideshowData[i].body, slideshowData[i].comments]
            //will return json object number, title, and description
            return retArray;
        }
    }  
}

function viewAll() { 
    document.getElementById("slideTitle").innerHTML = ""; 
    document.getElementById("slideText").innerHTML = ""; 
    document.getElementById("slideTitle").innerHTML = `<b>All phases:</b>`;
    for(var i = 0; i<slideshowData.length; i++)
    {
        document.getElementById("slideText").innerHTML += `<b>${slideshowData[i].title}:</b> ${slideshowData[i].body}`;
        document.getElementById("slideText").innerHTML += `<br></br>`;
    }
    document.getElementById("prev").style.visibility = "hidden";
    document.getElementById("next").style.visibility = "hidden";
}

function setSlideNum(n) { 
    var index = 0; 
    if(n<0) { 
        index=slideshowData.length-1; 
        currSlide=slideshowData.length-1;
    }
    else if(n>slideshowData.length-1)
    {
        index=0; 
        currSlide=0;
    }
    else 
    {
        index=n; 
        currSlide=n;
    }
    console.log("curr slide: " + currSlide + ", " + slideshowData.length + ": length");
    document.getElementById("prev").style.visibility = "visible";
    document.getElementById("next").style.visibility = "visible";
    if (currSlide==0) {
        console.log("Hide prev"); 
        document.getElementById("prev").style.visibility = "hidden"; }
    if (currSlide==slideshowData.length-1) {
        console.log("Hide next"); 
        document.getElementById("next").style.visibility = "hidden"; }
    document.getElementById("slideTitle").innerHTML = `<b>${slideshowData[index].title}</b>`;
    document.getElementById("slideText").innerHTML =`${slideshowData[index].body}`;
}

function toggleNav() {
    isToggle = !isToggle; 
    !isToggle ? document.getElementById("sideText").style.display = 'none' : document.getElementById("sideText").style.display = 'block';
}

function closeNav() {
    document.getElementById("mySidenav").style.width = "0px";
}

export function CarouselCards(props) {
    return (
        <>
        
        <p><b>R</b>eview what was supposed to happen:</p>
        <div id="slideshowCard" className="card">
        
        <div id="hamburger" className="hamburger, navbar-brand">
          <span id="hamburger" className="hamburger" onClick={() => toggleNav()}>&#9776;</span>
        </div>

        <div className="float-container">
        <div id="sideText" className="card-body, slides, float-child-left" style={{"display":"none"}}>
            {
                slideshowData.map((data, index) => (
                <p onClick={() => setSlideNum(index)}>{data.title}</p>
            ))}
            <a onClick={() => viewAll()}>ALL PHASES</a>
        </div>
        <div className="card-body, slides, float-child">
            <h5 id="slideTitle" className="card-title"><b>{slideshowData[loop(currSlide)[0]].title}</b></h5>
            <h6 className="card-subtitle mb-2 text-muted"></h6>
            <p id="slideText" className="card-text">{slideshowData[loop(currSlide)[0]].body}</p>
        </div>
        </div>

        <div className="arrows">
        <a id="prev" className="prev" style={{"visibility": "hidden"}}onClick={() => setSlideNum(currSlide-1)} >{"<"}</a> 
        <a id="next" className="next" onClick={() => setSlideNum(currSlide+1)} >{">"}</a> 
        </div>
        </div>
        </>
    );
}

export default CarouselCards;
