import React from "react";
import { stepData } from "../steps";
import { benchmarkInfo } from "../benchmarksInfo"; 
import {
    Link
  } from "react-router-dom";

var click = 0; 

function loop(n) { 
    console.log("In loop: " + n);
    for (var i in stepData) {
        console.log(stepData[i].step + ", props: " + n);
        if(stepData[i].step == n) {
            console.log(stepData[i].title);
            var retArray = [i, stepData[i].title, stepData[i].title]
            //will return json object number, title, and description
            return retArray;
        }
    }  
}

function setClicked(n){ 
    console.log("clicked: " + n)
    click = n; 
    document.getElementById("descriptionCard").innerHTML = ""; 
    document.getElementById("descriptionCardTitle").innerHTML = `<u>${benchmarkInfo[click].action}</u>` + ":";
    for(var i = 0; i<benchmarkInfo[click].management.length; i++){
        document.getElementById("descriptionCard").innerHTML += `<li> ${benchmarkInfo[click].management[i]}</li>`;
    }
}

export function TwoDivOutline(props) {
    return (
        <>
        <p>Describe the <b>{stepData[loop(props.name)[0]].title.charAt(0)}</b>{stepData[loop(props.name)[0]].title.substring(1, stepData[loop(props.name)[0]].length)}</p>
        <div class="container">
        <div class="card-group">
            <div class="card">
                <div class="card-block">
                    <div class="card-body">
                        <p class="card-title">Below are management actions you were supposed to perform in this scenario:</p>
                        <p class="card-text">
                            <ol>{ benchmarkInfo.map((data, index) => (<Link><li onClick={() => setClicked(index)} style={{"color": "black"}}><u>{data.action}</u></li></Link>))}</ol>

                        </p>
                    </div>
                </div>
            </div>
            <div class="card" style={{"flex-grow": "2", "margin-left":"20px", "margin-right":"20px"}}>
                <div class="card-block">

                    <div class="card-body>">
                        <p>Click on a management action to see performance benchmarks for this action:</p>
                        <p id="descriptionCardTitle" class="card-title"></p>
                        <ol id="descriptionCard"></ol>
                    </div>
                </div>
            </div>
            
        </div>
        </div>
        </>
    );
}

export default TwoDivOutline;